body {
  background-color: #e6fcf5;
}
h1 {
  text-align: center;
  color: #38d9a9;
  font-size: 48px;
  margin: 30px 0 0 0;
  user-select: none;
}

img {
  display: block;
  margin: 0 auto;
  width: 380px;
  height: 380px;
}

footer {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.credit {
  text-decoration: none;
  color: #c3fae8;
}

.register-btn {
  display: block;
  margin: 0 auto;
  margin-bottom: 15px;
  /* background-color: #627d98; */
  background: #bcccdc;
  /* background: #84c5f4; */
  border-color: #334e68;
}

.register-btn:hover,
.register-btn:active {
  background: #9fb3c8;

  border-color: #334e68;
}

.register-btn:focus {
  outline: none;
}

.register-btn:disabled,
.register-btn:focus:disabled,
.register-btn:hover:disabled,
.register-btn:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}

.login-btn {
  display: block;
  margin: 0 auto;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.back-btn {
  background: none;
  border-color: #adb5bd;
  color: #adb5bd;
  margin-right: 20px;
}

.back-btn:hover,
.back-btn:active {
  background: none;
  border-color: #adb5bd;
  color: #adb5bd;
}

.back-btn:focus {
  outline: none;
}

.back-btn:disabled,
.back-btn:focus:disabled,
.back-btn:hover:disabled,
.back-btn:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}

.action-buttons-container {
  display: flex;
  width: 100%;
  justify-content: center;
  margin: 25px 0 15px 0;
}

.logout-btn {
  padding: 0.5rem 2rem;
  font-size: 16px;
  background: #adb5bd;
  border-color: #868e96;
}

.logout-btn:hover,
.logout-btn:active {
  background: #adb5bd;

  border-color: #868e96;
}

.logout-btn:focus {
  outline: none;
}

.logout-btn:disabled,
.logout-btn:focus:disabled,
.logout-btn:hover:disabled,
.logout-btn:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}

.new-member-btn {
  padding: 0.5rem 2rem;
  font-size: 16px;
  background: #e64980;
  border-color: #a61e4d;
}

.new-member-btn:hover,
.new-member-btn:active {
  background: #d6336c;

  border-color: #a61e4d;
}

.new-member-btn:focus {
  outline: none;
}

.new-member-btn:disabled,
.new-member-btn:focus:disabled,
.new-member-btn:hover:disabled,
.new-member-btn:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}
