.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.backdrop:hover {
  cursor: pointer;
}

.modal {
  position: fixed;
  top: 10vh;
  left: 30%;
  width: 40%;
  z-index: 100;
  /* overflow: hidden; */
  padding-bottom: 50px;
  overflow-y: scroll;
  max-height: 100%;
  /* max-width: 100%; */
  overflow-x: hidden;
}

.header {
  background: #4f005f;
  padding: 1rem;
}

.header h2 {
  margin: 0;
  color: white;
}

.content {
  padding: 1rem;
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}

